.Input {

    width:100%;
    padding:10px;
    box-sizing: border-box;

}

.Label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;

}


.InputElement{
    width:100%;
    display: block;
    outline:none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 4px 8px;
    box-sizing: border-box;

}

.InputElement:focus{
    outline: none;
    background-color: #ccc;
}

.Invalid{
    border:1px solid red;
    background-color: #FDA49A;
}